import { useState, useEffect } from 'react'
import { supabase } from './supabaseClient'
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';

const Account = ({ session }) => {
  const [loading, setLoading] = useState(true)
  const [username, setUsername] = useState(null)
  const [website, setWebsite] = useState(null)
  const [avatar_url, setAvatarUrl] = useState(null)

  useEffect(() => {
    getProfile()
  }, [session])

  const getProfile = async () => {
    try {
      setLoading(true)
      const { user } = session

      let { data, error, status } = await supabase
        .from('profiles')
        .select(`username, website, avatar_url`)
        .eq('id', user.id)
        .single()

      if (error && status !== 406) {
        throw error
      }

      if (data) {
        setUsername(data.username)
        setWebsite(data.website)
        setAvatarUrl(data.avatar_url)
      }
    } catch (error) {
      alert(error.message)
    } finally {
      setLoading(false)
    }
  }

  const updateProfile = async (e) => {
    e.preventDefault()

    try {
      setLoading(true)
      const { user } = session

      const updates = {
        id: user.id,
        username,
        website,
        avatar_url,
        updated_at: new Date(),
      }

      let { error } = await supabase.from('profiles').upsert(updates)

      if (error) {
        throw error
      }
    } catch (error) {
      alert(error.message)
    } finally {
      setLoading(false)
    }
  }

  return (
    <div aria-live="polite">
      {loading ? (
        'Saving ...'
      ) : (
        <form onSubmit={updateProfile} className="form-widget">
          <div>Email: {session.user.email}</div>
          <div>
            <br/>
            <TextField id="username" label="Name" variant="outlined" value={username || ''} onChange={(e) => setUsername(e.target.value)} />
          </div>
          <div>
            <br/>
            <TextField id="website" label="Website" variant="outlined" value={website || ''} onChange={(e) => setWebsite(e.target.value)} />
          </div>
          <div>
            <Button variant="contained" className="button primary block" disabled={loading} type="submit">Update profile</Button>
          </div>
        </form>
      )}
      <Button variant="text" className="button block" type="button" onClick={() => supabase.auth.signOut()}>Sign Out</Button>
    </div>
  )
}

export default Account